<template>
  <div class="Portfolio_two">
    <h2 class="Portfolio-title_two">Portfolio</h2>
  </div>
  <div class="container">
    <div class="Portfolio">
      <div class="Portfolio-line">
        <h3 class="Portfolio-title">Gallery</h3>
        <div class="line">
          <div class="line__two"></div>
        </div>
      </div>
      <ul class="Portfolio-item">
        <li class="Portfolio__link" :class="{ active: all }" @click="allActive">
          All
        </li>
        <li
          class="Portfolio__link"
          :class="{ active: webApp }"
          @click="webAppActive"
        >
          Web-Applications
        </li>
        <li
          class="Portfolio__link"
          :class="{ active: webSite }"
          @click="webSiteActive"
        >
          Web-Sites
        </li>
      </ul>

      <div class="all__maket">
        <a
          v-for="link in allLink"
          :key="link.id"
          :href="link.href"
          target="_blank"
          :class="link.class"
          ref="linksRef"
        >
          <img class="All_img" :src="link.img" alt="" />
        </a>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from "vue";
import imgWeather from "@/assets/img/Screenshot_9.jpg";
import imgProweb from "@/assets/img/Screenshot_1.jpg";
import imgAirPods from "../assets/img/Screenshot_2.jpg";
import imgCinePhile from "../assets/img/Screenshot_5.png";
import imgVueCinema1 from "../assets/img/Vue-cinema.jpg";
import imgZone from "../assets/img/Zone.png";
import imgCalc from "../assets/img/Calc.png";
import imgNikeshop from "../assets/img/Nikeshop.png";
import imgName from "../assets/img/Name.png";
import imgTotembo from "../assets/img/Totembo.png";

const allLink = reactive([
  {
    id: 1,
    href: "https://wexrizadamm.github.io/weather-vuex/",
    class: ["all__link", "web-app"],
    img: imgWeather,
  },
  {
    id: 2,
    href: "https://wexrizadamm.github.io/Proweb/",
    class: ["all__link", "web-site"],
    img: imgProweb,
  },
  {
    id: 3,
    href: "https://wexrizadamm.github.io/AirPods/",
    class: ["all__link", "web-site"],
    img: imgAirPods,
  },
  {
    id: 4,
    href: "https://wexrizadamm.github.io/CinePhile/",
    class: ["all__link", "web-site"],
    img: imgCinePhile,
  },
  {
    id: 5,
    href: "https://github.com/Wexrizadamm/Vue-Cinema1.git",
    class: ["all__link", "web-site"],
    img: imgVueCinema1,
  },
  {
    id: 6,
    href: "https://github.com/Wexrizadamm/Zone.git",
    class: ["all__link", "web-site"],
    img: imgZone,
  },
  {
    id: 7,
    href: "https://github.com/Wexrizadamm/Calc.git",
    class: ["all__link", "web-site"],
    img: imgCalc,
  },
  {
    id: 8,
    href: "https://github.com/Wexrizadamm/Nikeshop.git",
    class: ["all__link", "web-site"],
    img: imgNikeshop,
  },
  {
    id: 9,
    href: "https://github.com/Wexrizadamm/Name.git",
    class: ["all__link", "web-site"],
    img: imgName,
  },
  {
    id: 10,
    href: "https://github.com/Wexrizadamm/Totembo.git",
    class: ["all__link", "web-site"],
    img: imgTotembo,
  },
]);

const linksRef = ref([]);
const all = ref(false);
const webApp = ref(false);
const webSite = ref(false);

const allActive = () => {
  all.value = true;
  webApp.value = false;
  webSite.value = false;
  linksRef.value.forEach((item) => {
    item.classList.add("active");
  });
};

const webAppActive = () => {
  all.value = false;
  webApp.value = true;
  webSite.value = false;
  linksRef.value.forEach((item, i) => {
    item.classList.remove("active");
    item.classList.forEach((itemTwo) => {
      if (itemTwo == "web-app") {
        item.classList.add("active");
      }
    });
  });
};
const webSiteActive = () => {
  all.value = false;
  webApp.value = false;
  webSite.value = true;
  linksRef.value.forEach((item, i) => {
    item.classList.remove("active");
    item.classList.forEach((itemTwo) => {
      if (itemTwo == "web-site") {
        item.classList.add("active");
      }
    });
  });
};

onMounted(() => {
  allActive();
});
</script>

<style lang="scss">
