  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px;

  @include media(333) {
    gap: 10px;
    margin-top: 15px;
  }
  @include media(340) {
    gap: 7px;
    margin-top: 10px;
    justify-content: center;
  }
}

.all__link {
  display: none;
}

.Portfolio_two {
  border-top: 2px solid #333333;
  border-bottom: 2px solid #333333;
}

.Portfolio-title_two {
  width: 453px;
  color: #fff;
  font-family: Poppins;
  font-size: 55px;
  font-style: normal;
  font-weight: 700;
  line-height: 35px;
  padding: 40px 62px;

  @include media(480) {
    padding: 20px 93px;
    font-size: 44px;
  }

  @include media(348) {
    padding: 15px 95px;
    font-size: 44px;
  }
}

.all__link.active {
  display: block;
}

.line {
  max-width: 127px;
  width: 100%;
  background: #353535;
  height: 7px;
}

