  height: 7px;
  background: red;
}

.All_img {
  width: 288px;
  height: 170px;
  flex-shrink: 0;
  border-radius: 25px;
  object-fit: cover;

  @include media(373) {
    width: 310px;
    height: 210px;
  }
  @include media(342) {
    width: 220px;
    height: 150px;
  }
}

.Portfolio-item {
  display: flex;
  gap: 49px;

  @include media(453) {
    gap: 20px;
  }

  @include media(355) {
    gap: 10px;
  }
  @include media(342) {
    gap: 5px;
  }
}

.Portfolio__link {
  color: #fff;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 35px;
}

.Portfolio__link:hover {
  color: red;
}

.Portfolio_button:hover {
  border: 3px solid red;
}

