  cursor: pointer;
}

.Portfolio {
  padding-left: 123px;
  display: flex;
  flex-direction: column;

  @include media(891) {
    padding-left: 80px;
  }

  @include media(825) {
    padding-left: 50px;
  }

  @include media(825) {
    padding-left: 30px;
  }

  @include media(397) {
    padding-left: 20px;
  }

  @include media(383) {
    padding-left: 10px;
  }

  @include media(373) {
    padding-left: 0px;
  }
}

.Portfolio-title {
  padding-top: 20px;
  color: #fff;
  font-family: Poppins;
